.Bio {
    display: grid;
    grid-template-columns: 1fr 2fr;
    width: 80%;
    -ms-flex-align: center;
        align-items: center;
    justify-items: center;
}

.Text {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    font-size: 12pt;
}

.Text p {
    padding: 0 10px;
    color: #292929;
}

.ProfileImg {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    max-width: 150px;
    width: 100%;
    height: auto;
}

.ProfileImg img {
    display: block;
    width: 100%;
    height: auto;
    border: 8px solid white;
    border-radius: 100%;
}

@media (max-width: 500px) {
    .Bio {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-flow: column;
            flex-flow: column;
        -ms-flex-pack: center;
            justify-content: center;
    }

    .Text p {
        text-align: center;
    }
}


